@import "../../../css/module/color";


$inputWidth: 440px;


@mixin centered($width , $height) {
	position: absolute;
	width: $width *1px;
	height: $height *1px;
	top: 50%;
	margin-top: $height * -0.5px;
	left: 50%;
	margin-left: $width * -0.5px;
}

body {
	background: {
		color: $red;
		image: url("weblogin.jpg");
		position: center center;
		repeat: no-repeat;
		size: 100% 100%;
	};
}


#loginPanel {
	@include centered(680 , 440);
	
	background-color: #f7f7f7;
	
	.logoPh{
		padding-top: 2.5em; padding-bottom: 2em;
	}
}


#avanda {
	//width: px;
	height: 80px;
	margin-top: 30px;
}


#mainSection{
	
	width: $inputWidth;
	text-align: center;
	margin: auto;
	
}


#loginForm {
	
	margin-bottom: 1em;
	
	input[type='text'], input[type='password'] {
		display: block;
		width: $inputWidth;
		height: 33px;
		line-height: 33px;
		margin-bottom: 10px;
		font-size: 14px;
		//text-indent: 3em;
		padding-left: 3em;
		border-radius: 3px;
		
		box-shadow:none !important;
	}
	
	input[type='text']{
		background: #fff  url("ico-user.png") no-repeat 1em center !important;
	}
	input[type='password']{
		background: #fff url("ico-lock.png") no-repeat 1em center !important;
	}
	
	input[type='text']:focus, input[type='password']:focus {
		//border-color: $border-color-active;
	}
	
}

#linkBar{
	height: 3em;
	line-height: 3em;
	
	label{
		margin-right: 0;
		img{
			margin-left: 1em;
		}
	}
}

#btnLogin {
	font-size: 14px;
	width: $inputWidth;
	height: 38px;
	background-color: $red;

	color: #fff;
	font-weight: 900;
}


#switcher{
	width: 54px;
	height: 26px;
	border:1px solid $red;
	border-radius: 3px;
	padding: 1px;
	background-color: #fff;
	position: absolute;
	top: 10px;
	right: 10px;
	white-space: nowrap;
	
	.tgr{
		display: inline-block;
		width: 23px;
		height: 22px;
		line-height: 22px;
		border-radius: 3px;
		cursor: pointer;
		
		&.cur{
			background-color:  $red;
			color: #fff;
			cursor: default;
		}
	}
	
}
